body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
}

.form-wrap {
  height: 55px;
  background-color: rgba(6, 6, 158, .933);
  justify-content: center;
  align-items: center;
  display: flex;
}

.search-form {
  width: 400px;
  margin: 0 auto;
  display: flex;
}

.search-form > input {
  height: 35px;
  border: 0;
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
  flex-grow: 1;
  padding-left: 15px;
  font-size: 16px;
}

.search-btn {
  width: 45px;
  height: 35px;
  background-color: #e7dede;
  border: 0;
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
  justify-content: center;
  align-items: center;
  transition: background-color .25s linear;
  display: flex;
}

.search-btn:hover, .search-btn:focus {
  background-color: #b6bcdf;
}

.search-icon {
  transition: transform .25s linear;
}

.search-icon:hover, .search-icon:focus {
  transform: scale(1.15);
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: rgba(6, 6, 158, .933);
  border: none;
  border-radius: 3px;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .25s linear;
  display: block;
}

.load-more:hover, .load-more:focus {
  background-color: rgba(89, 89, 168, .933);
}

